export default {
	props:{
		item:Object,
		index:Number
	},
	data() {
		return {
			isguanzhu: this.item.isguanzhu,
			infonum:this.item.infonum
		}
	},
	methods:{
		// 关注
		guanzhu(){
			this.isguanzhu=true;
			uni.showToast({
				title: '关注成功',
			});
		},
		// 顶踩
		caozuo(type){
			var me = this;
			switch (type){
				case "ding":
				if(me.infonum.index==1){ return; }
				me.infonum.dingnum++;
				if(me.infonum.index==2){
					me.infonum.cainum--;
				}
				me.infonum.index=1;
					break;
				case "cai":
				if(me.infonum.index==2){ return; }
				me.infonum.cainum++;
				if(me.infonum.index==1){
					me.infonum.dingnum--;
				}
				me.infonum.index=2;
					break;
			}
		},
		// 进入详情页
		opendetail(){
			console.log("进入详情页")
		}
	}
}
